<template>
  <!--v-card class="white lighten-4 elevation-0"-->
    <!--v-card-text-->
      <!--v-container fluid-->

        <!--v-card class="white lighten-4 elevation-1 " -->
          <router-link to="/search" >
            <v-layout row @click="select_source_input" v-ripple.mouseover.20 class="button is-primary"  >
              <v-flex xs4 >
                <v-subheader><!-- Skyscrapers icon by Icons8 -->
<img class="icon icons8-Skyscrapers" width="35" height="35" src="data:image/png;base64,"></v-subheader>
              </v-flex>
              <v-flex xs8 >
                <v-text-field
                  name="input-1"
                  label="Source"
                  id="testing"
                  v-model = "selected_source.name"
                  readonly="readonly"
                  style="color:rgba(21, 46, 72, 0.93)"
                ></v-text-field>
              </v-flex>
            </v-layout>
          </router-link>
        <!--/v-card-->

      <!--/v-container-->
    <!--/v-card-text-->
  <!--/v-card-->
</template>

<script>

import {mapMutations} from 'vuex'
import {mapGetters} from 'vuex'

export default {
  data(){
    return {

    }
  },
  methods:{
    ...mapMutations([
      'select_source_input'
    ]),
  },
  computed: {
  ...mapGetters([
    'selected_source'
  ]),
  }
}
</script>

<style scoped>
.input-group {
  height:50px;
}

</style>



